// base layout
export default [
  // 404 页面配置, 暂时跳回到主页
  {
    path: '*',
    component: () => import(/* webpackChunkName: "home" */ '@/views/Home.vue'),
    meta: {
      auth: false
    }
  },
  {
    path: '/',
    name: 'home',
    component: () => import(/* webpackChunkName: "home" */ '@/views/Home.vue'),
    meta: {
      auth: false
    }
  },
  {
    path: '/login',
    name: 'login',
    component: () => import(/* webpackChunkName: "login" */ '@/views/Login.vue'),
    meta: {
      auth: false
    }
  },
  {
    path: '/register',
    name: 'register',
    component: () => import(/* webpackChunkName: "register" */ '@/views/Register.vue'),
    meta: {
      auth: false
    }
  }
]
