import Vue from 'vue'

// 注册全局自定义指令 `v-mousewheel`
Vue.directive('focus', {
  // 当被绑定的元素插入到 DOM 中时调用
  inserted: function (el, binding) {
    let handle = (e) => {
      let event = e || window.event
      let direct = 1
      // 只关心方向, 不关心速度
      if (event.wheelDelta) {
        direct = event.wheelDelta > 0 ? 1 : -1
      } else if (event.detail) {
        // firefox
        direct = event.detail < 0 ? 1 : -1
      }
      // 此处绑定的value应为一个 function
      binding.value(direct)
    }
    // 绑定事件
    if (el.addEventListener) {
      // chrome firefox
      el.addEventListener('DOMMouseScroll', handle, false)
    } else {
      // ie chrome
      el.onmousewheel = handle
    }
  }
})
