/**
 * 聊天窗口数据仓库(后期应该使用第三方控件)
 */
const state = {
  isShow: false // 是否展示
}

const getters = {}
const actions = {}
const mutations = {
  /**
   * 展示聊天窗口
   * @param {Object} state : store state
   */
  showChat (state) {
    state.isShow = true
  },
  /**
   * 隐藏聊天窗口
   * @param {Object} state : store state
   */
  hideChat (state) {
    state.isShow = false
  }
}

export default {
  namespaced: true,
  state,
  getters,
  actions,
  mutations
}
