import Vue from 'vue'

import {
  Layout, // 布局
  Menu, // 菜单
  Breadcrumb, // 面包屑
  Carousel, // 走马灯
  Icon, // 图标
  Form, // form
  Input, // input
  Radio, // input
  InputNumber, // input-number
  Select, // Select
  Cascader, // Cascader
  DatePicker, // DatePicker
  Button, // 按钮
  Row, // 栅格
  Col, // 栅格
  Upload, // 上传
  Avatar, // 头像
  Steps, // 步骤条
  Table, // 表格
  Card, // 表格
  Tabs, // 表格
  Tag, // tag
  List, // 表格
  Modal, // 弹出框
  Rate, // 评分
  Comment, // 评价
  Drawer, // 抽屉
  Tree, // 评价
  Collapse, // 折叠面板
  LocaleProvider // 国际化
} from 'ant-design-vue'

Vue.use(Form)
Vue.use(Select)
Vue.use(DatePicker)
Vue.use(Layout)
Vue.use(Menu)
Vue.use(Breadcrumb)
Vue.use(Input)
Vue.use(Radio)
Vue.use(Steps)
Vue.use(Drawer)
Vue.use(Upload)
Vue.use(Table)
Vue.use(Card)
Vue.use(List)
Vue.use(Tabs)
Vue.use(Comment)
Vue.use(Collapse)
Vue.use(Tree)
Vue.use(Tag)
Vue.use(Modal)
Vue.component(Rate.name, Rate)
Vue.component(Cascader.name, Cascader)
Vue.component(InputNumber.name, InputNumber)
Vue.component(Avatar.name, Avatar)
Vue.component(Row.name, Row)
Vue.component(Col.name, Col)
Vue.component(Button.name, Button)
Vue.component(Carousel.name, Carousel)
Vue.component(Icon.name, Icon)
Vue.component(LocaleProvider.name, LocaleProvider)

Vue.prototype.$confirm = Modal.confirm
