<template>
  <!-- ant design 组件国际货 -->
  <a-locale-provider :locale="locale">
    <!-- 全站采用上中下布局, 暂不需要其他布局 -->
    <base-layout id="app">
      <router-view/>
    </base-layout>
    <!-- 工具条 -->
  </a-locale-provider>
</template>

<script>
import zhCN from 'ant-design-vue/lib/locale-provider/zh_CN'
import BaseLayout from '@/components/layouts/BaseLayout'

export default {
  name: 'app',
  components: { BaseLayout },
  data () {
    return {
      locale: zhCN
    }
  }
}
</script>

<style lang="less">
@import "~@/assets/styles/app";
</style>
