<template>
  <transition enter-active-class="animated fadeInUp" leave-active-class="animated fadeOutDown">
    <div v-show="isShow" class="web-chat-modal">
      <div class="chat-header">
        <a-avatar :size="22" icon="user" style="margin: -4px .5em 0;"/>
        <span>客服: 伍六七</span>
        <a-icon
          style="cursor: pointer; float: right; font-size: 18px; color: #000;margin: .5em;"
          @click.native="hideWebChat"
          type="down"
        />
      </div>
      <ul class="chat-content">
        <li
          v-for="(content, index) in chatData"
          :key="content.src + index"
          class="chat-item"
          :class="`chat-${content.src}`"
        >
          <span>{{content.content}}</span>
        </li>
      </ul>
      <div class="chat-option">
        <a-textarea
          v-model="enterTxt"
          placeholder="请输入......"
          :autosize="{ minRows: 3, maxRows: 3 }"
          @pressEnter="sendMsg"
          style="border: none; box-shadow: none; resize: none;"
        />
        <div style="text-align: right;">
          <a-button size="small" type="primary" @click="sendMsg">发送</a-button>
        </div>
      </div>
    </div>
  </transition>
</template>

<script>
import { mapState } from 'vuex'
export default {
  name: 'webChat',
  computed: {
    ...mapState('webChat', ['isShow'])
  },
  data () {
    return {
      enterTxt: '',
      chatData: [{ src: 'serve', content: '您好，很高兴为您服务。' }]
    }
  },
  methods: {
    /**
     * 隐藏聊天窗口
     */
    hideWebChat () {
      this.$store.commit('webChat/hideChat')
    },
    /**
     * 发送消息
     */
    sendMsg () {
      this.chatData.push({
        src: 'me',
        content: this.enterTxt
      })
      this.enterTxt = ''
    }
  }
}
</script>

<style scoped lang="less">
.web-chat-modal {
  width: 300px;
  background-color: #fff;
  box-shadow: 0 0 8px rgba(0, 0, 0, 0.2);
  position: fixed;
  bottom: 4px;
  right: 4px;
  z-index: 100;

  .chat-header {
    background-color: #ddd;
    height: 40px;
    line-height: 40px;
  }
  .chat-content {
    height: 360px;
    list-style: none;
    padding: 0;
    margin: 0;
    .chat-item {
      font-size: 12px;
      margin: 1em;
      line-height: 1.6;

      span {
        padding: 4px 6px;
        border: 1px solid #ccc;
        border-radius: 4px;
        box-shadow: 0 0 10px rgba(0, 0, 0, 0.4);
      }

      &.chat-serve span {
        background-color: #f0f0f0;
      }
      &.chat-me {
        text-align: right;
        color: #fff;
        span {
          background-color: #4d7be0;
        }
      }
    }
  }
  .chat-option {
    border-top: 1px solid #ccc;
  }
}
</style>
