<template>
  <div class="base-article-list">
    <h2 class="article-list-title">{{title}}</h2>
    <a-list :dataSource="dataSrc" :pagination="pagination">
      <a-list-item slot="renderItem" slot-scope="item, index">
        <a slot="actions" to="/help?id" href="javascript:;" @click="showDrawer">详情</a>
        <a-list-item-meta description="这是新闻描述这是新闻描述这是新闻描述这是新闻描述这是新闻描述这是新闻描述这是新闻描述....">
          <a slot="title" href="javascript:;" @click="showDrawer">
            {{item.title}}
            <a-tag v-if="index < 2" color="red">HOT</a-tag>
            <a-tag v-if="index < 3" color="pink">NEW</a-tag>
          </a>
        </a-list-item-meta>
      </a-list-item>
    </a-list>
    <a-drawer
      title="新闻详情"
      width="70%"
      @close="onClose"
      :visible="drawerVisible"
      :wrapStyle="{height: 'calc(100% - 108px)',overflow: 'auto',paddingBottom: '108px'}"
    >
      <base-article style="padding: 0; box-shadow: none;">
        <slot name="article"></slot>
      </base-article>
    </a-drawer>
  </div>
</template>
<script>
export default {
  name: 'baseArticleList',
  props: {
    title: String,
    dataSrc: Array
  },
  data () {
    return {
      drawerVisible: false,
      pagination: {
        onChange: (page) => {
          console.log(page)
        },
        pageSize: 3
      }
    }
  },
  methods: {
    showDrawer () {
      this.drawerVisible = true
    },
    onClose () {
      this.drawerVisible = false
    }
  },
  created () {
    // this.showDrawer()
  }
}
</script>

<style scoped>
.base-article-list {
  background-color: #fff;
  padding: 40px;
}
.article-list-title {
  font-size: 26px;
  text-align: center;
  margin: 0 0 0.5em;
  line-height: 1;
}
</style>
