import Vue from 'vue'
import Router from 'vue-router'
import AboutRouteData from './about-route-data'
import BaseRouteData from './base-route-data'
import MainRouteData from './main-route-data'

Vue.use(Router)

const router = new Router({
  // mode: 'history',
  base: process.env.BASE_URL,
  routes: BaseRouteData
})

// main layout 的路由配置
router.addRoutes([{
  path: '/layout-main',
  name: 'layoutMain',
  component: () => import('@/components/layouts/MainLayout'),
  children: MainRouteData
}])

// main layout 的路由配置
router.addRoutes([{
  path: '/layout-about',
  name: 'layoutAbout',
  component: () => import('@/components/layouts/AboutLayout'),
  children: AboutRouteData
}])

export default router
